import React,{Component} from 'react'
import { Spin, Form, Select, Input, Button, message, Icon } from 'antd';
import { hashHistory } from 'react-router';
const FormItem = Form.Item;
const { TextArea } = Input;
const Option = Select.Option;

export class EditForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false
        };
        ['handleSubmit',  'resetForm'].forEach(m=>this[m] = this[m].bind(this))
    }

    componentDidMount() {
        let that = this;
        window.K.create('#content', {
            allowFileManager : true,
            afterChange: function() {
                that.props.form.setFieldsValue({"content" :this.html()});
            }
        });
    }

    handleSubmit() {
        this.props.form.validateFields((err, values) => {
            if(!err) {
                ajax({
                    url: "/user/mail/send",
                    method: "post",
                    data: values,
                    callback: function(data) {
                        if(data.status == 200) {
                            message.success("发送成功!", 1);
                        } else {
                            message.error(data.errMsg, 1);
                        }
                    }
                })
            }
        })
    }

    resetForm() {
        this.props.form.resetFields();
    }

    goToHistory() {
        hashHistory.push("/user/mail/history");
    }

    render() {
        const { getFieldDecorator } = this.props.form;
        const formItemLayout = {
            labelCol: { span: 3 },
            wrapperCol: { span: 8 }
        };
        return (
            <Spin spinning={this.state.loading} tip="Loading">
                <Form>
                    <FormItem
                        {...formItemLayout}
                        label="邮件标题"
                        hasFeedback
                    >
                        {getFieldDecorator('title', {
                            rules: [{
                                required: true,
                                message: "请输入邮件的标题!"
                            }]
                        })(
                            <Input placeholder="请输入邮件标题" />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="邮件内容"
                        hasFeedback
                    >
                        {getFieldDecorator('content', {
                            rules: [
                                {
                                    required: true,
                                    message: "请输入内容!"
                                }
                            ]
                        })(
                            <TextArea placeholder="这里输入内容" autosize={{ minRows: 6 }} />
                        )}
                    </FormItem>
                    <FormItem
                        {...formItemLayout}
                        label="发送给"
                        hasFeedback
                    >
                        {getFieldDecorator('sendTo', {
                            initialValue: "0"
                        })(
                            <Select>
                                <Option value="0">所有</Option>
                                <Option value="1">资金方</Option>
                                <Option value="2">项目方</Option>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem wrapperCol={{ span: 8, offset: 3 }}>
                        <Button icon="enter" type="primary" onClick={this.handleSubmit.bind(this,1)} style={{"marginRight": "20px"}}>发送</Button>
                        <Button icon="reload" onClick={this.resetForm} style={{"marginRight": "20px"}}>重置</Button>
                        <Button icon="sync" onClick={this.goToHistory} style={{"marginBottom": "20px"}}>查看历史</Button>
                    </FormItem>
                </Form>
            </Spin>
        )
    }
}

const WrappedEditForm = Form.create({})(EditForm);

module.exports = WrappedEditForm;


